.formcond,.formcond2 {
  width: 100%;
  height: 56px;
  border-radius: 4px;
  position: relative;
  background-color: rgba(255,255,255,0.3);
  transition: 0.3s all;
  text-align: center;


}
.formcond.focussed input {
  padding: 24px 16px 8px 16px;
}
.formcond.focussed input + label,.formcond2.focussed input {
  top: 4px;
  opacity: 1;
  color: #28cc70;
}
.formcond.locked ,.formcond2.locked{
 pointer-events: none;
}
.formcond input ,.formcond2 input{
  width: 100%;
  height: 56px;
  position: relative;
  padding: 0px 16px;
  border: none;
  border-radius: 4px;
  font-family: 'Gotham SSm A', 'Gotham SSm B', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  background-color: transparent;
  color: #282828;
  outline: none;
  box-shadow: 0px 4px 20px 0px transparent;
  transition: 0.3s background-color ease-in-out, 0.3s box-shadow ease-in-out, 0.1s padding ease-in-out;
  -webkit-appearance: none;
}
.formcond input::-webkit-input-placeholder,.formcond2 input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.8);
}
.formcond input::-moz-placeholder,.formcond2 input::-moz-placeholder {
  color: rgba(255, 255, 255, 0.8);
}
.formcond input:-ms-input-placeholder,.formcond2 input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.8);
}
.formcond input:-moz-placeholder,.formcond2 input:-moz-placeholder {
  color: rgba(255, 255, 255, 0.8);
}
.formcond:hover,.formcond2:hover {
  background-color: rgba(255, 255, 255, 0.45);
  box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.05);
}

